import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Projectcard from "./Projectcard.js";
import Fade from "react-reveal/Fade";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "lightgray",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 64,
      height: "92.5vh",
    },
  },
  content: {
    [theme.breakpoints.up("sm")]: {
      display: "flex",
    },
  },
  wrapper1: {
    [theme.breakpoints.up("sm")]: {
      width: "24%",
      paddingTop: "5%",
      paddingLeft: "3%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "90%",
      paddingTop: "5%",
      marginLeft: "5%",
      marginRight: "5%",
    },
    // backgroundColor: "red",
  },
  wrapper2: {
    [theme.breakpoints.up("sm")]: {
      width: "24%",
      paddingTop: "25%",
      paddingLeft: "2%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "90%",
      paddingTop: "5%",
      marginLeft: "5%",
      marginRight: "5%",
    },
    // backgroundColor: "green",
  },
  wrapper3: {
    [theme.breakpoints.up("sm")]: {
      width: "24%",
      paddingTop: "3%",
      paddingLeft: "2%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "90%",
      paddingTop: "5%",
      marginLeft: "5%",
      marginRight: "5%",
    },
  },
  wrapper4: {
    [theme.breakpoints.up("sm")]: {
      width: "24%",
      paddingTop: "2%",
      paddingLeft: "2%",
      // paddingRight: "3%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "90%",
      paddingTop: "5%",
      marginLeft: "5%",
      marginRight: "5%",
    },
  },
  upperCard: {
    [theme.breakpoints.up("sm")]: {
      height: "40vh",
    },
    [theme.breakpoints.down("sm")]: {
      paddingBottom: "5%",
    },
  },
  shadow: {
    boxShadow: "10px 10px 5px black",
  },
  wrapper4Second: {
    [theme.breakpoints.up("sm")]: {
      height: "52vh",
    },
    [theme.breakpoints.down("sm")]: {
      paddingBottom: "5%",
    },
  },
}));

export default function Kontakt() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <div className={classes.content}>
        <div className={classes.wrapper1}>
          <Fade top>
            <Projectcard
              heading={"Selling-market Webapplication"}
              url={"https://github.com/henning410/unibay"}
              description={
                <span>
                  <Typography paragraph>Together with 3 mates we created this webapplication. I was responsible for full-stack-development and project management.</Typography>
                  <Typography paragraph>technical features:</Typography>
                  <Typography paragraph>
                    - login-system with keycloak and saml protocol
                    <br />- usage of material-UI and bootstrap
                    <br />- connection with mongoDB and usage of mongoose
                    <br />- realtimechat with socket.io, timeagoJS, react-emoji and scroll-to-bottom
                    <br />- usage of docker
                    <br />- usage of google maps API for reverse geocoding
                  </Typography>
                </span>
              }
            />
          </Fade>
        </div>
        <div className={classes.wrapper2}>
          {/* <div className={classes.shadow}> */}
          <Fade bottom>
            <Projectcard
              heading={"Virtual-Reality game for Oculus Go"}
              url={"https://github.com/henning410/vr-game"}
              description={
                <span>
                  <Typography paragraph>created with javascript and the threeJS library.</Typography>
                </span>
              }
            />
          </Fade>
          {/* </div> */}
        </div>
        <div className={classes.wrapper3}>
          <div className={classes.upperCard}>
            {/* <div className={classes.shadow}> */}
            <Fade top>
              <Projectcard
                heading={"Mobile-Game created with javascript canvas"}
                url={"https://github.com/henning410/mobile-game"}
                description={
                  <span>
                    <Typography paragraph>
                      This mobile game was created only with javascript canvas. The focus here is on the different touch interaction like touch, hold and swipe.{" "}
                    </Typography>
                  </span>
                }
              />
            </Fade>
            {/* </div> */}
          </div>
          {/* <div className={classes.shadow}> */}
          <Fade bottom>
            <Projectcard
              heading={"Jodel-WebApp with React, Java & Quarkus Hibernate ORM"}
              url={"https://github.com/henning410/jodel-webapp"}
              description={
                <span>
                  <Typography paragraph>
                    This webapplication was created with Java 11 and Quarkus Hibernate. I used ReactJS for the frontend and the framework material-UI. The frontend is connected
                    with the backend where I have a MySQL database.{" "}
                  </Typography>
                </span>
              }
            />
          </Fade>
          {/* </div> */}
        </div>
        <div className={classes.wrapper4}>
          <div className={classes.wrapper4Second}>
            {/* <div className={classes.shadow}> */}
            <Fade top>
              <Projectcard
                heading={"Browser-Game with JavaScript"}
                url={"https://www2.hs-esslingen.de/~heweit00/it-game/it-game.html"}
                description={
                  <span>
                    <Typography paragraph>
                      During my study, one professor gave us the task to develop a game that he made up himself. On the board are randomly spawning atoms. Atoms are affecting the
                      fields near them. So when the player shoots a laser from the border into the board, it possibly gets distracted by some atoms and comes out on another
                      location. The task of the player is to guess all atoms correctly.
                    </Typography>
                  </span>
                }
              />
            </Fade>
            {/* </div> */}
          </div>
          {/* <div className={classes.shadow}> */}
          <Fade bottom>
            <Projectcard
              heading={"Website with pure HTML and CSS"}
              url={"https://www2.hs-esslingen.de/~heweit00"}
              description={
                <span>
                  <Typography paragraph>I created a copy of our university homepage with pure HTML and CSS. This project was part of a modul in the 3rd semester.</Typography>
                </span>
              }
            />
          </Fade>
          {/* </div> */}
        </div>
      </div>
    </div>
  );
}
