import React from "react";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import MuiListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router";
import ContactMailIcon from "@material-ui/icons/ContactMail";
import AccountBoxIcon from "@material-ui/icons/AccountBox";
import BuildIcon from "@material-ui/icons/Build";
import AccountTreeIcon from "@material-ui/icons/AccountTree";
import SchoolIcon from "@material-ui/icons/School";
import { purple } from "@material-ui/core/colors";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
    color: "white",
    backgroundColor: "#232423",
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none",
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    background: "#232423",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(1),
    backgroundColor: "red",
    height: "100%",
  },
  drawerContent: {
    marginTop: "10%",
  },
  listitem: {
    [theme.breakpoints.up("sm")]: {
      paddingLeft: "12%",
      paddingRight: "5%",
      paddingTop: "20%",
      paddingBottom: "20%",
    },
  },
}));

const ListItem = withStyles({
  root: {
    "&:hover": {
      backgroundColor: purple[500],
    },
  },
})(MuiListItem);

const ResponsiveDrawer = (props) => {
  console.log(props);
  const { history } = props;
  const { window } = props;

  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [text, setText] = React.useState("About me");

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/homepage");
              setText("About me");
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <AccountBoxIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={"About me"} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/skills");
              setText("Skills");
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <BuildIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={"Skills"} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/myprojects");
              setText("My Projects");
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <AccountTreeIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={"My Projects"} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/journey");
              setText("Work Experience");
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <SchoolIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={"Work Experience"} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/contact");
              setText("Contact");
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <ContactMailIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={"Contact"} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton color="inherit" aria-label="open drawer" edge="start" onClick={handleDrawerToggle} className={classes.menuButton}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            {text}
          </Typography>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === "rtl" ? "right" : "left"}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
    </div>
  );
};

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default withRouter(ResponsiveDrawer);
// export default ResponsiveDrawer;
