import "./App.css";
import ResponsiveDrawer from "./ResponsiveDrawer.js";
import { Route, Switch } from "react-router-dom";
import AboutMe from "./AboutMe.js";
import Skills from "./Skills.js";
import MyProjects from "./MyProjects.js";
import Berufserfahrung from "./Berufserfahrung.js";
import Kontakt from "./Kontakt.js";

export default function App() {
  return (
    <div>
      <ResponsiveDrawer />
      <Switch>
        <Route exact path="/homepage" render={(props) => <AboutMe {...props} />} />
        <Route exact path="/skills" render={(props) => <Skills {...props} />} />
        <Route exact path="/myprojects" render={(props) => <MyProjects {...props} />} />
        <Route exact path="/journey" render={(props) => <Berufserfahrung {...props} />} />
        <Route exact path="/contact" render={(props) => <Kontakt {...props} />} />
      </Switch>
    </div>
  );
}
