import { TextField } from "@material-ui/core";
import React, { useState } from "react";
import { useFormControls } from "./ContactFormControls.tsx";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
// import { purple } from "@material-ui/core/colors";
import Snackbar from "@material-ui/core/Snackbar";
import Alert from "@material-ui/lab/Alert";
import "./button.scss";
import { SocialIcon } from "react-social-icons";
import Zoom from "react-reveal/Zoom";
import { createMuiTheme, responsiveFontSizes, ThemeProvider } from "@material-ui/core/styles";

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

theme.typography.h4 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "1.3rem",
  },
};

theme.typography.h6 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "0.8rem",
  },
};

const useStyles = makeStyles((theme) => ({
  root: {
    // backgroundColor: "red",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 10,
      marginRight: 10,
      marginTop: 100,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 100,
      display: "grid",
      justifyContent: "space-evenly",
    },
  },
  form: {
    [theme.breakpoints.up("sm")]: {
      marginTop: 10,
      padding: 20,
      height: 400,
    },
    [theme.breakpoints.down("sm")]: {
      marginTop: 10,
      padding: 10,
      height: 400,
    },

    // background: "linear-gradient(45deg, #4298f5 30%, #0051a8 90%)",
    color: "#0051a8",
  },
  contactForm: {
    boxShadow: "10px 10px 5px black",
    border: "1px solid black",
  },
  contactDetail: {
    [theme.breakpoints.up("sm")]: {
      marginTop: 64,
      padding: 20,
      boxShadow: "10px 10px 5px blue",
    },
    [theme.breakpoints.down("sm")]: {
      marginTop: 40,
      padding: 10,
    },
    border: "1px solid black",
    background: "linear-gradient(45deg, #4298f5 30%, #0051a8 90%)",
    color: "white",
  },
  buttonDiv: {
    [theme.breakpoints.up("sm")]: {
      marginTop: 20,
      padding: 20,
    },
    [theme.breakpoints.down("sm")]: {
      padding: 20,
    },
  },
  sendMessage: {
    color: "green",
  },
  item: {
    [theme.breakpoints.up("sm")]: {
      padding: 10,
    },
    // backgroundColor: "lightgray",
  },
  Icon: {
    // backgroundColor: "red",
    width: "10px",
  },
  socialMedia: {
    [theme.breakpoints.up("sm")]: {
      padding: 10,
      // backgroundColor: "lightblue",
      display: "flex",
      justifyContent: "space-evenly",
    },
    [theme.breakpoints.down("sm")]: {
      // backgroundColor: "lightblue",
      display: "flex",
      justifyContent: "space-evenly",
      marginBottom: 0,
    },
  },
  socialText: {
    [theme.breakpoints.up("sm")]: {
      marginTop: 50,
      padding: 20,
      // backgroundColor: "lightblue",
      display: "flex",
      justifyContent: "space-evenly",
    },
    [theme.breakpoints.down("sm")]: {
      // marginTop: 54,
      padding: 20,
      // backgroundColor: "lightblue",
      display: "flex",
      justifyContent: "space-evenly",
    },
  },
}));

const inputFieldValues = [
  {
    name: "fullName",
    label: "firstname and surname",
    id: "my-name",
  },
  {
    name: "email",
    label: "mail address",
    id: "my-email",
  },
  {
    name: "message",
    label: "message",
    id: "my-message",
    multiline: true,
    rows: 10,
  },
];

// const ColorButton = withStyles((theme) => ({
//   root: {
//     color: theme.palette.getContrastText(purple[500]),
//     backgroundColor: "#4298f5",
//     color: "white",
//     "&:hover": {
//       backgroundColor: purple[500],
//       color: "white",
//     },
//   },
// }))(Button);

export default function Kontakt() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const { handleInputValue, handleFormSubmit, formIsValid, errors } = useFormControls();

  return (
    <div className={classes.root}>
      <div className={classes.contactForm}>
        <form autoComplete="off" onSubmit={handleFormSubmit}>
          <div className={classes.form}>
            <ThemeProvider theme={theme}>
              <Typography variant="h4" align="center">
                SEND ME A MESSAGE
              </Typography>
            </ThemeProvider>
            {inputFieldValues.map((inputFieldValue, index) => {
              return (
                <TextField
                  key={index}
                  onChange={handleInputValue}
                  onBlur={handleInputValue}
                  name={inputFieldValue.name}
                  label={inputFieldValue.label}
                  error={errors[inputFieldValue.name]}
                  multiline={inputFieldValue.multiline ?? false}
                  fullWidth
                  rows={inputFieldValue.rows ?? 1}
                  autoComplete="none"
                  {...(errors[inputFieldValue.name] && {
                    error: true,
                    helperText: errors[inputFieldValue.name],
                  })}
                />
              );
            })}
          </div>
          <div className={classes.buttonDiv}>
            <button
              type="submit"
              onClick={() => {
                setOpen(true);
              }}
              className="button"
              disabled={!formIsValid()}
              data-label="Click ME"
            >
              Send formular
            </button>
          </div>
        </form>
      </div>

      <div className={classes.socialText}>
        <Typography variant="h5">OR FIND ME ON SOCIAL MEDIA</Typography>
      </div>
      <div className={classes.socialMedia}>
        <Zoom>
          <SocialIcon url="https://www.instagram.com/henning_weise/" />
          <SocialIcon url="https://de.linkedin.com/" />
          <SocialIcon url="https://discord.com/" />
          <SocialIcon url="https://www.whatsapp.com/?lang=de" />
          <SocialIcon url="https://github.com/henning410" />
        </Zoom>
      </div>

      <Snackbar autoHideDuration={4000} open={open} onClose={() => setOpen(false)}>
        <Alert severity="success" variant="filled">
          Request send successfully
        </Alert>
      </Snackbar>
    </div>
  );
}
