import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import image from "./images/HÃ–NING.png";
import { createMuiTheme, responsiveFontSizes, ThemeProvider } from "@material-ui/core/styles";
import Fade from "react-reveal/Fade";

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

theme.typography.h4 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "1.3rem",
  },
};

theme.typography.h6 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "0.8rem",
  },
};

const useStyles = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 64,
    },
  },
  upperContent: {
    color: "white",
    [theme.breakpoints.up("sm")]: {
      display: "flex",
      justifyContent: "left",
      marginLeft: "3%",
      marginRight: "3%",
      // height: "93.2vh",
    },
    background: "hsl(204 100% 5%)",
    boxShadow: "10px 5px 5px black",
    fontFamily: "system-ui",
  },
  aboutme: {
    // backgroundColor: "red",
    [theme.breakpoints.up("sm")]: {
      width: 430,
      marginLeft: "15%",
      paddingTop: "5%",
      paddingBottom: "5%",
      marginRight: "5%",
    },
    [theme.breakpoints.down("sm")]: {
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "15%",
      paddingBottom: "5%",
    },
  },
  lowerContent: {
    color: "black",
    // backgroundColor: purple[500],
    [theme.breakpoints.up("sm")]: {
      // height: "47.4vh",
      display: "flex",
      justifyContent: "space-evenly",
    },
  },
  image: {
    [theme.breakpoints.up("sm")]: {
      width: "100%",
      height: "auto",
    },
    [theme.breakpoints.down("sm")]: {
      width: "60%",
      height: "auto",
      display: "block",
      marginLeft: "auto",
      marginRight: "auto",
    },
  },
  picture: {
    [theme.breakpoints.up("sm")]: {
      width: "40vh",
      paddingTop: "3%",
      paddingLeft: "3%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "10%",
      alignItems: "center",
    },
    // backgroundColor: "red",
  },
  beschreibung: {
    color: "linear-gradient(to right, hsl(98 100% 62%), hsl(204 100% 59%))",
    [theme.breakpoints.up("sm")]: {
      paddingTop: "5%",
      width: "30%",
    },
    [theme.breakpoints.down("sm")]: {
      width: 400,
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "5%",
    },
    // backgroundColor: "red",
  },
  special: {
    [theme.breakpoints.up("sm")]: {
      width: 430,
      marginLeft: "5%",
      paddingTop: "2%",
    },
    [theme.breakpoints.down("sm")]: {
      width: 400,
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "5%",
    },
  },
  goodToKnow: {
    [theme.breakpoints.up("sm")]: {
      marginLeft: "5%",
      paddingTop: "2%",
    },
    [theme.breakpoints.down("sm")]: {
      width: 400,
      paddingTop: "1%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "5%",
    },
  },
}));

export default function AboutMe() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <div className={classes.upperContent}>
        {/* <Canvas> */}
        <div className={classes.picture}>
          <img src={image} className={classes.image} alt="Picture_Of_Henning" />
        </div>
        <div className={classes.beschreibung}>
          <ThemeProvider theme={theme}>
            <Typography variant="h2" component="h1" align="center" gutterBottom>
              Henning Weise
            </Typography>
            <Typography variant="h4" component="h1" align="center" gutterBottom>
              Software Engineering <br />
              and <br />
              Media Computing
            </Typography>
          </ThemeProvider>
        </div>
        <div className={classes.aboutme}>
          <Typography variant="h5" component="h1" gutterBottom>
            About me
          </Typography>
          <Typography variant="body1" component="h1" gutterBottom align="justify">
            I'm studying right now in the 5th semester at the University of Applied Sciene Esslingen. Currently I'm doing an internship and work as a web-developer. What do you
            need to know about me? I love coding, so offside university I also code a lot for myself as you can see with this homepage ;) I am doing in my freetime a lots of sports
            like diving, skiing and doing gymnastics.
          </Typography>
        </div>
        {/* </Canvas> */}
      </div>

      <div className={classes.lowerContent}>
        <div className={classes.special}>
          <Typography variant="h4" component="h1" gutterBottom>
            What makes me special?
          </Typography>
          <Fade bottom>
            <Typography variant="h6" component="h1" gutterBottom>
              - learning fast
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - open minded
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - team spirit
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - organizational talent
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - reliable
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - high willingness to work
            </Typography>
          </Fade>
        </div>
        <div className={classes.goodToKnow}>
          <Typography variant="h4" component="h1" gutterBottom>
            Facts about me
          </Typography>
          <Fade bottom>
            <Typography variant="h6" component="h1" gutterBottom>
              - native language german
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - fluently english
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - basic knowledge french
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - german scolarship since automn 2021
            </Typography>
            <Typography variant="h6" component="h1" gutterBottom>
              - gymnast in the 2nd Bundesliga
            </Typography>
          </Fade>
        </div>
      </div>
    </div>
  );
}
